.App {
  display: flex;
  text-align: center;
  align-items: center;
  flex-direction: column;
}

.sheet {
  width: 60rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 3rem;
  margin-top: 3rem;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.box {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  width: 50rem;
}

.mr10 {
  border: none;
  width: 97.5%;
}

.mr10::placeholder {
  text-align: left;
}

input {
  padding: 5px 10px;
}

.btn-save {
  width: 50rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgb(180, 189, 192);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 1rem;
}
.btn-save:hover {
  background-color: rgb(131, 137, 141);
}

.print {
  width: 50rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
   background-color: rgb(180, 189, 192);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
  margin-bottom: 20px;
}
.print:hover {
  background-color: rgb(131, 137, 141);
}

.btn-add {
  width: 50rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgb(180, 189, 192);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
}
.btn-add:hover {
   background-color: rgb(131, 137, 141);
}

.btn-remove {
  width: 50rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgba(218, 30, 30, 0.952);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
}
.btn-remove:hover {
  background-color: rgb(182, 9, 3);
}

.btn-edit {
  width: 50rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgba(196, 196, 196, 0.952);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
}
.btn-edit:hover {
  background-color: rgb(55, 174, 253);
}

.textSection {
  width: 50rem;
  text-align: left;
}

.categoryText {
  font-size: 17px;
  margin-right: 1.5px;
  font-weight: bold;
}

h2 {
  font-size: 2rem;
  color: rgb(218, 30, 30, 0.952);
  width: 50rem;
}

hr{
  border: solid 1px;
}
